import {Link, useParams} from 'react-router-dom';
import {useState, useEffect} from 'react';
import { Helmet } from 'react-helmet';

import MarvelService from '../../services/MarvelService';
import Spinner from '../spinner/Spinner';
import Error404 from './Error404';

import './singleCharacterPage.scss';
import './singleComicPage.scss';
import '../charInfo/charInfo.scss';

const SingleCharacterPage = () => {

    const {charId} = useParams();
    const [char, setChar] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(false);

    const marvelService = new MarvelService();

    useEffect(() => {
        updateChar();
    }, [charId]);

    const updateChar = () => {
        onCharLoading();
        marvelService
            .getCharacter(charId)
            .then(onCharLoaded)
            .catch(onError);
    }

    const onCharLoaded = (char) => {
        setChar(char);
        setLoading(false);
    }

    const onCharLoading = () => {
        setLoading(true);
    }

    const onError = () => {
        setError(true);
        setLoading(false);
    }

    const errorMessage = error ? <Error404/> : null;
    const spinner = loading ? <Spinner/> : null;
    const content = !(loading || error || !char) ? <View char={char}/> : null;

    return (
        <>
            {errorMessage}
            {spinner}
            {content}
        </>
    )
}

const View = ({char}) => {

    const {name, description, thumbnail, homepage, wiki, comics} = char;

    return (
        <>
            <div className="single-comic">
                <Helmet>
                    <meta
                        name="description"
                        content={`${name} character book`}
                    />
                    <title>{name}</title>
                </Helmet>
                <img src={thumbnail} alt={name} className="single-comic__char-img"/>
                <div className="single-comic__info">
                    <h2 className="single-comic__name">{name}</h2>
                    <p className="single-comic__descr">{description}</p>
                    <div className="char__btns">
                            <a href={homepage} target='blank' className="button button__main" style={{marginRight: '20px'}}>
                                <div className="inner">homepage</div>
                            </a>
                            <a href={wiki} target='blank' className="button button__secondary">
                                <div className="inner">Wiki</div>
                            </a>
                        </div>
                </div>
                <Link to="/" className="single-comic__back">Back to all</Link>
            </div>
            <h2 style={{ marginTop: '30px'}}>Comics:</h2>
            <ul className="char__comics-list" style={{ marginTop: '20px'}}>
                {comics.length > 0 ? null : 'There is no comics with this character'}
                {
                    comics.map((item, i) => {
                        // eslint-disable-next-line
                        return (
                            <li key={i} className="char__comics-item">
                                <Link to={`/comics/${item.resourceURI.match(/\d+/g).slice(1)}`} target='_blank' className="char__comics-item-link">{item.name}</Link>
                            </li>
                        )
                    })
                }                
            </ul>
        </>
    )
}

export default SingleCharacterPage;